import { Component, Element, Event, EventEmitter, Host, Listen, Prop, Watch, h } from '@stencil/core';

const isHTMLMdxFlyoutItemElement = (element: string | HTMLMdxFlyoutItemElement): element is HTMLMdxFlyoutItemElement => {
  if (typeof element === 'object' && typeof element.item === 'string') {
    return true;
  }

  return false;
};

@Component({
  tag: 'mdx-flyout',
  styleUrl: 'flyout.scss',
  shadow: true,
})
export class Flyout {
  @Element()
  host: HTMLMdxFlyoutElement;

  @Prop({ reflect: true })
  selectedItem: HTMLMdxFlyoutItemElement['item'];

  @Watch('selectedItem')
  onFlyoutChange() {
    this.flyoutChange.emit(this.selectedItem);
  }

  @Event()
  flyoutChange: EventEmitter<HTMLMdxFlyoutItemElement['item']>;

  @Listen('keydown', { capture: true, target: 'window' })
  handleKeyDown(e: KeyboardEvent) {
    if (!(e.key === 'ArrowDown' || e.key === 'ArrowUp')) {
      return;
    }

    const items = this.items;
    let currentIndex = items.findIndex((item) => item === document.activeElement);

    if (currentIndex === -1) {
      return;
    }

    if (e.key === 'ArrowDown') {
      currentIndex = (currentIndex + 1) % items.length;
    } else if (e.key === 'ArrowUp') {
      currentIndex = (currentIndex - 1 + items.length) % items.length;
    }

    items[currentIndex].focusItem();
    e.preventDefault();
  }

  private select(item: string) {
    if (this.selectedItem === item) {
      return false;
    }
    this.selectedItem = item;
    return true;
  }

  private get items() {
    return Array.from(this.host.querySelectorAll('mdx-flyout-item'));
  }

  private getItems(item: string | HTMLMdxFlyoutItemElement): HTMLMdxFlyoutItemElement | undefined {
    return isHTMLMdxFlyoutItemElement(item) ? item : this.items.find((t) => t.item === item);
  }

  componentWillRender() {
    const selectedItem = this.getItems(this.selectedItem);
    this.items.forEach((item) => {
      item.selected = false;
    });
    if (selectedItem) {
      selectedItem.selected = true;
    }
  }

  render() {
    return (
      <Host
        onItemClick={({ detail: { item } }) => {
          this.select(item);
        }}
      >
        <div class="flyout">
          <slot></slot>
        </div>
      </Host>
    );
  }
}
